	module.exports = {"data":{"site":{"siteMetadata":{"title":"signalwerk"}},"markdownRemark":{"html":"<h1 id=\"raleway\"><a href=\"#raleway\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Raleway</h1>\n<p><small>Nicole Watrinet</small></p>\n<div class=\"col1to12\">\n<p><img src=\"/IAD2017.schriftmuster/Raleway-4aa23986e42ab3e6fdbf31011119a6e8.svg\" alt=\"Raleway\"></p>\n</div>\n<ol>\n<li>Oberlänge ist minim höher wie die Versalhöhe</li>\n<li>Abschluss bei a und u sind sehr kurz</li>\n<li>Sehr Minime Verjüngung vom Duktus</li>\n<li>Zweistöckiges g mit kurzer Schlaufe</li>\n<li>Sehr konstruierte runde Formen, ausserdem hat das e einen Überbiss</li>\n<li>Minim verkürztes t im Gegensatz zu Versahlbuchstaben + rechter Strich ist länger</li>\n<li>Abschlüsse immer schräg</li>\n</ol>\n<h2 id=\"design\"><a href=\"#design\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Design</h2>\n<p>Die Raleway Schrift wurde von diversen Designern konzipiert.</p>\n<p>Der Anfang machte der amerikanische Designer Matt McInerney, indem er nur den Schriftschnitt «Thin» und «Thin Italic» entwickelte. Dieser Schnitt wurde vor allem für grossformatige Nutzen entworfen. Zwei weitere Designer, Pablo Impallari und Rodrigo Fuenzalida, haben im Jahr 2012 die Schriftfamilie um 8 weitere Schriftstärken erweitert und zusätzlich mit der dazugehörigen «Italic»-Schriftlage ergänzt.</p>\n<p>Der Schriftname setzt sich aus den Buchstaben, welche den grössten Charakter aufweisen, zusammen:</p>\n<ul>\n<li>Das «e» hat einen Überbiss</li>\n<li>Das «w» kreuzt sich in der Mitte</li>\n<li>Das kleine «L» ähnelt einem Krückstock</li>\n</ul>\n<p>Die Raleway fällt in die Kategorie der Grotesk Schrift (San Serif oder auch als serifenlose Schrift bezeichnet). Um eine bessere Lesbarkeit zu erreichen, greift Raleway viel von geometrischen Buchstabenformen auf. Da sie deshalb fast keine Verjüngungen mehr aufweist, wird sie oft auch als Neo-Grotesk bezeichnet.\nAusserdem gibt es einen Geschwister-Font namens Raleway Dots.</p>\n<h2 id=\"designer\"><a href=\"#designer\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Designer</h2>\n<p>{{Text}}</p>\n<h4 id=\"quellen\"><a href=\"#quellen\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Quellen</h4>\n<ol>\n<li><a href=\"https://www.pcworld.com/article/232420/raleway.html\">pcworld.com</a></li>\n<li><a href=\"https://fonts.google.com/specimen/Raleway\">Google Fonts</a></li>\n<li><a href=\"https://www.theleagueofmoveabletype.com/raleway\">theleagueofmoveabletype.com</a></li>\n</ol>","frontmatter":{"date":"April 05, 2018","title":"Raleway"}}},"pathContext":{"slug":"/fonts/nicole/"}}

